	export default {
		data() {
			return {
				list: [],
				loading: false,
				finished: false
			}
		},
		onLoad() {

			this.init()

		},
		onReachBottom() {
			this.init()
		},
    methods: {
		/**
		 * 模拟数据更新
		 */
		init() {
			// 这里包含了滚动加载，文档地址 https://youzan.github.io/vant/#/zh-CN/list
			
			if (this.finished) {
				return
			}
			
			// 异步更新数据
			setTimeout(() => {
				for (let i = 0; i < 10; i++) {
					this.list.push(this.list.length + 1);
				}
				// 加载状态结束
				this.loading = false;

				// 数据全部加载完成
				if (this.list.length >= 40) {
					this.finished = true;
				}
		    }, 500);
		},
		/**
		 * 去审批事项详情页面
		 * @param {any} 传入的对应审批事项的信息  
		 */
		goLabDetails(item) {
			console.log('传入的审批事项的信息是', item)
			uni.navigateTo({
				url: `./approval-details?item=${item}`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
		
    }
	}
